@keyframes glassesFrame{
    from{
        stroke-dashoffset: 1500;
    }
    to{
        stroke-dashoffset: -1400;
    }
}
@keyframes glassesLeft{
    from{
        stroke-dashoffset: -500;
    }
    to{
        stroke-dashoffset: 1400;
    }
}
@keyframes glassesRight{
    from{
        stroke-dashoffset: 1100;
    }
    to{
        stroke-dashoffset: -1000;
    }
}
@keyframes glassesAccent1{
    from{
        stroke-dashoffset: 1000;
    }
    to{
        stroke-dashoffset: -1000;
    }
}
@keyframes glassesAccent2{
    from{
        stroke-dashoffset: -100;
    }
    to{
        stroke-dashoffset: 1900;
    }
}
@keyframes glassesAccent3{
    from{
        stroke-dashoffset: -200;
    }
    to{
        stroke-dashoffset: 1800;
    }
}

.home {

    > .home-section {
        display: grid;
        grid-template-columns: 50% 50%;
        width: 100%;
        min-height: 100vh;
        top: 0;
        position: relative;

        .banner-img-wrap {
            @include flex-wrap( nowrap, row, start, center );
            position: relative;
            width: fit-content;
            height: 100vh;
            top: - $xxl;
            z-index: 2;
            mask: url('../masks/inkmask-inv.png');
            mask-size: 3300% 100%;
            animation: maskUp 1s steps(32) forwards;
        
            > img {
                height: 100%;
                aspect-ratio: 1 / 1;
            }
            .hat-overlay {
                position: absolute;
                z-index: 4;
                animation: hatGlow 1s linear infinite alternate;
            }
            .gif-overlay {
                z-index: 3;
            }
            .gif-bg {
                z-index: 1;
            }
            .banner-svg {
                height: 100%;
                z-index: 20;
                aspect-ratio: 1 / 1;
                position: absolute;
                z-index: 2;

                svg {
                    width: 100%;

                    .glasses {
                        fill: transparent;
                        stroke: #b2e4ff;
                        stroke-linecap: round;
                        stroke-linejoin: round;
                        stroke-width: 4px;
                    }
                    .glasses-frame {
                        stroke-dasharray: 1400;
                        animation: glassesFrame 4s infinite both;
                    }
                    .glasses-left {
                        stroke-dasharray: 1000;
                        animation: glassesLeft 4s infinite both;
                    }
                    .glasses-right {
                        stroke-dasharray: 1000;
                        animation: glassesRight 4s 0.3s infinite both;
                    }
                    .glasses-accent1 {
                        stroke-dasharray: 1000;
                        animation: glassesAccent1 4s 0.3s infinite both;
                    }
                    .glasses-accent2 {
                        stroke-dasharray: 1000;
                        animation: glassesAccent2 4s 0.3s infinite both;
                    }
                    .glasses-accent3 {
                        stroke-dasharray: 1000;
                        animation: glassesAccent3 4s 0.3s infinite both;
                    }
                }
            }

        }
        article {
            @include flex-wrap( nowrap, column, start, start );
            width: 100%;
            height: 100%;
            height: fit-content;
            z-index: 10;
            // overflow: hidden;

            h1 {
                mask: url('../masks/inkmask-left.png');
                mask-size: 100% 3300%;
                animation: maskLeft 1s steps(32) forwards;
            }
        }
        .window-wrap {
            @include flex-wrap( nowrap, row, start, start );
            height: 100%;
            padding-top: $xxl;

            .window, .window-bar, .window-bg, .bgbg {
                position: absolute;
                margin-left: - $lg;
                // width: 960px;
                width: 70%;
            }
            .window, .window-bar {
                animation: hatGlow 1s linear infinite alternate;
            }
            .window {
                z-index: 3;
            }
            .window-bar {
                z-index: 2;
            }
            .window-bg {
                z-index: 1;
            }
            .bgbg {
                background-color: green;
                aspect-ratio: 16 / 9;
                max-width: 65%;
                margin-top: $lg;
                margin-left: 0;
                // z-index: 90;
            }
        }
    }
    .logo-wrap {
        position: absolute;
        width: 100vh;
        height: 100%;
        top: 8vh;
        left: 30vh;
        opacity: 20%;
        overflow: hidden;
        z-index: 1;

        > svg {
            margin-top: 10%;
            scale: 2.5;
        }
    }
    .home-bg {
        @include flex-wrap( nowrap, row, center, center );
        position: fixed;
        width: 100%;
        height: 100vh;
        z-index: 1;
        filter: blur( 10px );
        margin-left: -35vw;

        .bg-front {
            animation: bgGrow 12s linear infinite;

            > div {
                animation: bgColor 12s linear infinite;
            }
        }
        .bg-mid1 {
            animation: bgGrow 12s 2s linear infinite;
            
            > div {
                animation: bgColor 12s 2s linear infinite;
            }
        }
        .bg-mid2 {
            animation: bgGrow 12s 4s linear infinite;

            > div {
                animation: bgColor 12s 4s linear infinite;
            }
        }
        .bg-mid3 {
            animation: bgGrow 12s 6s linear infinite;

            > div {
                animation: bgColor 12s 6s linear infinite;
            }
        }
        .bg-mid4 {
            animation: bgGrow 12s 8s linear infinite;

            > div {
                animation: bgColor 12s 8s linear infinite;
            }
        }
        .bg-back {
            animation: bgGrow 12s 10s linear infinite;

            > div {
                animation: bgColor 12s 10s linear infinite;
            }
        }
        .bg-rotate-right, .bg-rotate-left {
            @include flex-wrap( nowrap, row, center, center );
            position: absolute;
            width: 100%;
            height: 100vh;
        }
        .rotate-front {
            animation: bgRotateRight 12s linear infinite;
        }
        .rotate-mid1 {
            animation: bgRotateLeft 12s 2s linear infinite;
        }
        .rotate-mid2 {
            animation: bgRotateRight 12s 4s linear infinite;
        }
        .rotate-mid3  {
            animation: bgRotateLeft 12s 6s linear infinite;
        }
        .rotate-mid4  {
            animation: bgRotateRight 12s 8s linear infinite;
        }
        .rotate-back  {
            animation: bgRotateLeft 12s 10s linear infinite;
        }
        .star-wrap {
            @include flex-wrap( nowrap, row, center, center );
            position: absolute;
            width: 100%;
            height: 100%;

            > div {
                border-radius: 100px;
                position: absolute;
            }

            & :nth-child( 1 ) {
                width: $md;
                height: $md;
                transform: translate( 45vh, 45vh );
            }
            & :nth-child( 2 ) {
                width: $md;
                height: $md;
                transform: translate( -20vh, -30vh );
            }
            & :nth-child( 3 ) {
                width: $lg;
                height: $lg;
                transform: translate( 60vh, 40vh );
            }
            & :nth-child( 4 ) {
                width: $lg;
                height: $lg;
                transform: translate( -60vh, -40vh );
            }
            & :nth-child( 5 ) {
                width: $xl;
                height: $xl;
                transform: translate( 90vh, 25vh );
            }
            & :nth-child( 6 ) {
                width: $xl;
                height: $xl;
                transform: translate( -90vh, -25vh );
            }
            & :nth-child( 7 ) {
                width: $xl;
                height: $xl;
                transform: translate( 60vh, 5vh );
            }
            & :nth-child( 8 ) {
                width: $xl;
                height: $xl;
                transform: translate( -60vh, -5vh );
            }
            & :nth-child( 9 ) {
                width: $md;
                height: $md;
                transform: translate( 85vh, -5vh );
            }
            & :nth-child( 10 ) {
                width: $md;
                height: $md;
                transform: translate( -85vh, 5vh );
            }
            & :nth-child( 11 ) {
                width: $lg;
                height: $lg;
                transform: translate( 70vh, -25vh );
            }
            & :nth-child( 12 ) {
                width: $lg;
                height: $lg;
                transform: translate( -70vh, 25vh );
            }
            & :nth-child( 13 ) {
                width: $xxl;
                height: $xxl;
                transform: translate( 95vh, -40vh );
            }
            & :nth-child( 14 ) {
                width: $xxl;
                height: $xxl;
                transform: translate( -95vh, 40vh );
            }
            & :nth-child( 15 ) {
                width: $md;
                height: $md;
                transform: translate( 45vh, -45vh );
            }
            & :nth-child( 16 ) {
                width: $md;
                height: $md;
                transform: translate( -45vh, 45vh );
            }
            & :nth-child( 17 ) {
                width: $xl;
                height: $xl;
                transform: translate( 25vh, -35vh );
            }
            & :nth-child( 18 ) {
                width: $xl;
                height: $xl;
                transform: translate( -35vh, 0vh );
            }
            & :nth-child( 19 ) {
                width: $xl;
                height: $xl;
                transform: translate( -5vh, -40vh );
            }
            & :nth-child( 20 ) {
                width: $lg;
                height: $lg;
                transform: translate( 45vh, 25vh );
            }
        }
    }
}
@keyframes maskUp {
    0% {
        mask-position: 0 0;
    }
    100% {
        mask-position: 100% 0;
    }
}
@keyframes maskLeft {
    0% {
        mask-position: 0 100%;
    }
    100% {
        mask-position: 0 0;
    }
}
@keyframes hatGlow {
    0% {
        opacity: 100%;
    }
    10% {
        opacity: 80%;
    }
    20% {
        opacity: 100%;
    }
    30% {
        opacity: 90%;
    }
    40% {
        opacity: 100%;
    }
    50% {
        opacity: 70%;
    }
    60% {
        opacity: 80%;
    }
    70% {
        opacity: 100%;
    }
    80% {
        opacity: 90%;
    }
    90% {
        opacity: 70%;
    }
    100% {
        opacity: 100%;
    }
}
